<template>
    <div id="map"></div>
    <div class="bot1">
        <b-button pill variant="primary" @click="displayOleum">오름</b-button>
        <b-button pill variant="primary" @click="displayOlle">제주올레</b-button>
        <b-button pill variant="primary" @click="displayHalla">한라산 둘레길</b-button>
        <b-button pill variant="primary" @click="displayHiking">등산로</b-button>
        <b-button pill variant="primary" @click="displayzpt">제로포인트루트</b-button>
    </div>
    <div class="bot2">
        <b-button pill variant="success" @click="displayJejuCafe">제주카페</b-button>
        <b-button pill variant="success" @click="displayJejuRest">제주식당</b-button>
        <b-button pill variant="success" @click="displaySeogwipoCafe">서귀포카페</b-button>
        <b-button pill variant="success" @click="displaySeogwipoRest">서귀포식당</b-button>        
    </div>
    <div class="bot3">
        <b-button pill variant="secondary" @click="displaytest">다음맵에서 가져오기</b-button>
        <b-button pill variant="secondary" @click="myfileWrite">파일로 저장</b-button>
    </div>

</template>

<script>
import { toRaw } from "vue";
import {ref} from 'vue';

import oleum_data from "../assets/jeju_oleum.json"
import olle_data from "../assets/jeju_olle_trail.json"
import halla_data from "../assets/jeju_halla_trail.json"
import hiking_data from "../assets/jeju_hiking_total.json"
import zpt_data from "../assets/jeju_zpt.json"

import jeju_rest_all_data from "../assets/jeju_rest_all.json"
import jeju_cafe_all_data from "../assets/jeju_cafe_all.json"
import jejucafe_data from "../assets/jeju_favorites.json"


// import jeju_cafe from "../img/jeju_cafe.png"

var apikey= ref('');

var customOverlay=({});
var infoWindow=({});
var cataindex=0;
var hallaIndex=true;
var halla_polylines=[];
var olleIndex=true;
var olle_polylines=[];
var hikingIndex=true;
var hike_polylines=[];
var zptIndex=true;
var zpt_polylines=[];
var oleumIndex=true;
var oleum_circles=[];

var jejucafeIndex=true;
var jejucafeMarkers=[];
var jejurestIndex=true;
var jejurestMarkers=[];
var seogwipocafeIndex=true;
var seogwipocafeMarkers=[];
var seogwiporestIndex=true;
var seogwiporestMarkers=[];

var searchData=[];

export default {
    name: "KakaoMap",
    
    props:{
        height:String,
        width:String,
    },

    data() {
        return {            
        };
    },

    created() {
        // console.log(process.env.VUE_APP_KAKAOMAP_API_KEY);
        apikey = process.env.VUE_APP_KAKAOMAP_API_KEY;
        // console.log('api_key ', apikey);
    },

    mounted() {
        if (! window.kakao || !window.kakao.maps) {            
            const script = document.createElement("script");
            script.onload = () => kakao.maps.load(this.initMap);
            script.src ="//dapi.kakao.com/v2/maps/sdk.js?autoload=false&appkey="+apikey+'&libraries=services';
            document.head.appendChild(script);            
        } else {
            this.initMap();            
        }
    },

    methods: {        
        initMap() {

            const container = document.getElementById("map");            
            container.style.width = `${this.width}`;
            container.style.height = `${this.height}`;            
            const options = {
                center: new kakao.maps.LatLng(33.38, 126.58),
                level: 10,
            };
            this.map = new kakao.maps.Map(container, options), 
            customOverlay = new kakao.maps.CustomOverlay({}),
            infoWindow = new kakao.maps.InfoWindow({});
            this.map.relayout();
            
            // console.log(this.map);
            // toRaw(this.map).relayout();
        },

        displayOleum() {

            cataindex=1;
            if(oleumIndex) {

                if(oleum_circles.length <=0 ) {
                    let features = oleum_data.features;
                    features.forEach( element => {
                        let position = new kakao.maps.LatLng(element.geometry.coordinates[1], element.geometry.coordinates[0]);
                        let dia = element.properties['반경']; 
                        let name = element.properties['오름명']
                        this.drawingCircle(cataindex, toRaw(this.map), position, dia, name);                        
                    });                    
                } else {
                    oleum_circles.map( (cur) => cur.setMap(toRaw(this.map)));
                }
                oleumIndex = false;
            } else {
                oleum_circles.map( (cur) => cur.setMap(null));
                oleumIndex = true;
            }
            
        },

        displayOlle() {

            cataindex=1;
            if(olleIndex) {
                if( olle_polylines.length <=0 ) {
                    let features = olle_data.features;            
                    features.forEach( element => {      
                        let name = element.properties.name;             
                        let coord = element.geometry.coordinates;
                        coord.forEach( points => {
                            var path=[];
                            points.forEach ( point => {
                                let position = new kakao.maps.LatLng(point[1], point[0]);
                                path.push(position);
                            });
                            this.drawingPolyline(cataindex,toRaw(this.map), path, name);
                        });
                    });
                } else {
                    olle_polylines.map( (cur) => cur.setMap(toRaw(this.map)));
                }                
                olleIndex = false;
            } else {
                olle_polylines.map( (cur) => cur.setMap(null));
                olleIndex = true;
            }            
        },

        displayHalla() {

            cataindex=2; 
            if(hallaIndex) {
                if ( halla_polylines.length <=0 ) {
                    let features = halla_data.features;            
                    features.forEach( element => { 
                        let name = element.properties.name;  
                        let coord = element.geometry.coordinates;
                        coord.forEach( points => {
                            var path=[];
                            points.forEach ( point => {
                                let position = new kakao.maps.LatLng(point[1], point[0]);
                                path.push(position);
                            });
                            this.drawingPolyline(cataindex, toRaw(this.map), path, name);                     
                        });
                    });
                } else {
                    halla_polylines.map( (cur) => cur.setMap(toRaw(this.map)));
                }
                hallaIndex = false;
            } else {
                halla_polylines.map( (cur) => cur.setMap(null));
                hallaIndex = true;
            }
            
        },

        displayHiking() {

            cataindex=3;
            if(hikingIndex){
                if ( hike_polylines.length <=0 ) {                    
                    let features = hiking_data.features;
                    features.forEach( element => {                        
                        let name = '등산로 '+ element.properties.PMNTN_NM;                
                        let coord = element.geometry.coordinates;
                        coord.forEach( points => {
                            var path=[];
                            points.forEach ( point => {
                                let position = new kakao.maps.LatLng(point[1], point[0]);
                                path.push(position);
                            });
                            this.drawingPolyline(cataindex, toRaw(this.map), path, name);            
                        });
                    });
                } else {
                    hike_polylines.map( (cur) => cur.setMap(toRaw(this.map)));                    
                }
                hikingIndex = false;
            } else {
                hike_polylines.map( (cur) => cur.setMap(null));
                hikingIndex = true;
            }          
        },

        displayzpt(){

            cataindex=4;
            if(zptIndex){
                if ( zpt_polylines.length <=0 ) {                    
                    let features = zpt_data.features;
                    features.forEach( element => {                        
                        let name = element.properties.name;                
                        let coord = element.geometry.coordinates;

                        if ( element.geometry.type=='LineString' ) {                            
                            
                            var path=[];                            
                            coord.forEach( point => {
                                let position = new kakao.maps.LatLng(point[1], point[0]);
                                path.push(position);              
                            });
                            this.drawingPolyline(cataindex, toRaw(this.map), path, name);

                        } else if (element.geometry.type=='MultiLineString'){
                            
                            coord.forEach( points => {
                                var path=[];
                                points.forEach ( point => {
                                    let position = new kakao.maps.LatLng(point[1], point[0]);
                                    path.push(position);
                                });
                                this.drawingPolyline(cataindex, toRaw(this.map), path, name);            
                            });

                        }
                    });
                } else {
                    zpt_polylines.map( (cur) => cur.setMap(toRaw(this.map)));                    
                }
                zptIndex = false;
            } else {
                zpt_polylines.map( (cur) => cur.setMap(null));
                zptIndex = true;
            }

        },

        drawingCircle(cataindex, map, position, dia, name) {
            
            let fillrealcolor='';
            let backcolor='';
            
            if (cataindex==1) {
                fillrealcolor = '#E51D1A';
                backcolor = '#FF0000';      
            } else if (cataindex==2) {
                fillrealcolor = '#00FF0F';
                backcolor = '#FF0000';      
            } else if (cataindex==3) { 
                fillrealcolor = '#FF00FF';
                backcolor = '#0000FF';                      
            }  else if (cataindex==4) { 
                fillrealcolor = '#FF0000';
                backcolor = '#0000FF';
            }

            var circle = new kakao.maps.Circle({
                    map: map,
                    center : position,
                    radius: dia,    
                    strokeColor: '#5347AA',
                    strokeOpacity: 0.5,
                    strokeWeight: 2,
                    fillColor: fillrealcolor,
                    fillOpacity: 0.3,
                    // 원의 중심좌표 입니다 
                    // radius: 5000, // 미터 단위의 원의 반지름입니다 
                    // strokeWeight: 5, // 선의 두께입니다 
                    // strokeColor: '#75B8FA', // 선의 색깔입니다
                    // strokeOpacity: 1, // 선의 불투명도 입니다 1에서 0 사이의 값이며 0에 가까울수록 투명합니다
                    // strokeStyle: 'dashed', // 선의 스타일 입니다
                    // fillColor: '#CFE7FF', // 채우기 색깔입니다
                    // fillOpacity: 0.7  // 채우기 불투명도 입니다   
            });           
            
            var infoBox = new kakao.maps.CustomOverlay({
                content: name + '  [반경 '+dia+' m]',
                yAnchor: 1.5,
            });

            new kakao.maps.event.addListener(circle, 'mouseover', function(mouseEvent) {                                  
                circle.setOptions({
                    fillOpacity: 0.6,
                    strokeStyle: 'dashed'
                });
                infoBox.setPosition(mouseEvent.latLng);
                infoBox.setMap(map);
            })
            
            new kakao.maps.event.addListener(circle, 'mousemove', function(mouseEvent) {                
                infoBox.setPosition(mouseEvent.latLng);
                infoBox.setMap(map);
            });
            
            new kakao.maps.event.addListener(circle, 'mouseout', function() {                
                circle.setOptions({
                    fillOpacity: 0.3,
                    strokeStyle: 'line'
                });
                infoBox.setMap(null);   
            });

            if (cataindex==1) {
                oleum_circles.push(circle);
            } 
            // else if (cataindex==2) {
            //     halla_polylines.push(polyline);
            // } else if (cataindex==3) { 
            //     hike_polylines.push(polyline);
            // } else if (cataindex==4) { 
            //     zpt_polylines.push(polyline);
            // }  
        },

        drawingPolyline(cataindex, map, path, name) {

            let realcolor='';
            let backcolor='';

            if (cataindex==1) {
                realcolor = '#0004FF';
                backcolor = '#FF0000';      
            } else if (cataindex==2) {
                realcolor = '#00FF0F';
                backcolor = '#FF0000';      
            } else if (cataindex==3) { 
                realcolor = '#FF00FF';
                backcolor = '#0000FF';                      
            }  else if (cataindex==4) { 
                realcolor = '#FF0000';
                backcolor = '#0000FF';
            }

            var polyline = new kakao.maps.Polyline({
                map: map,
                path: path, // 선을 구성하는 좌표배열 입니다
                strokeWeight: 2, // 선의 두께 입니다
                strokeColor: realcolor, // 선의 색깔입니다
                strokeOpacity: 0.5, // 선의 불투명도 입니다 1에서 0 사이의 값이며 0에 가까울수록 투명합니다
                strokeStyle: 'solid'                 
            });            
            
            var infoBox = new kakao.maps.CustomOverlay({
                content: name,
                yAnchor: 1,
            });

            new kakao.maps.event.addListener(polyline, 'mouseover', function(mouseEvent) {                                  
                polyline.setOptions({
                    strokeWeight: 7,
                    strokeColor: backcolor,
                    strokeOpacity: 1,
                    strokeStyle: 'solid'
                });
                infoBox.setPosition(mouseEvent.latLng);
                infoBox.setMap(map);
                // console.log('ddd ', infoBox);
            })
            
            new kakao.maps.event.addListener(polyline, 'mousemove', function(mouseEvent) {                
                // customOverlay.setPosition(mouseEvent.latLng); 
                infoBox.setPosition(mouseEvent.latLng);
                infoBox.setMap(map);
            });
            
            new kakao.maps.event.addListener(polyline, 'mouseout', function() {                
                polyline.setOptions({
                    strokeWeight: 2, // 선의 두께 입니다
                    strokeColor: realcolor, // 선의 색깔입니다
                    strokeOpacity: 0.5, // 선의 불투명도 입니다 1에서 0 사이의 값이며 0에 가까울수록 투명합니다
                    strokeStyle: 'solid' 
                })
                infoBox.setMap(null);   
            });

            if (cataindex==1) {
                olle_polylines.push(polyline);
            } else if (cataindex==2) {
                halla_polylines.push(polyline);
            } else if (cataindex==3) { 
                hike_polylines.push(polyline);
            } else if (cataindex==4) { 
                zpt_polylines.push(polyline);
            }  
        },

        displayJejuCafe(){  
            cataindex=10;          
            if(jejucafeIndex){
                if ( jejucafeMarkers.length <=0 ) {
                    
                    let features = jeju_cafe_all_data.features;
                    features.forEach( element => {
                        console.log(element)   
                        let name = element.place_name;                
                        let my_address = element.address_name;
                        let city = element.address_name.split(' ')[1];
                        let my_link = element.place_url;
                        var position = new kakao.maps.LatLng(element.y, element.x);
                        let phone = element.phone;                        
                        if( city=='제주시') {
                            this.drawingMarker(cataindex, toRaw(this.map), position, name, my_address, my_link, phone);
                        }                        
                    });
                    
                    // let features = jejucafe_data.features;
                    // features.forEach( element => {                        
                    //     let name = element.properties.Name;                
                    //     let my_point = element.geometry.coordinates;
                    //     let my_address = element.properties.address;
                    //     let my_link = element.properties.link;
                    //     let my_layer = element.properties.layer;
                    //     var position = new kakao.maps.LatLng(my_point[1], my_point[0]);                        
                    //     if( my_layer=='제주 카페') {                            
                    //         this.drawingMarker(cataindex, toRaw(this.map), position, name, my_address, my_link);
                    //     }                        
                    // });
                } else {
                    jejucafeMarkers.map( (cur) => cur.setMap(toRaw(this.map)));                    
                }
                jejucafeIndex = false;
            } else {
                jejucafeMarkers.map( (cur) => cur.setMap(null));
                jejucafeIndex = true;
            }
        },

        displayJejuRest(){
            cataindex=11;
            if(jejurestIndex){
                if ( jejurestMarkers.length <=0 ) {                    
                    let features = jeju_rest_all_data.features;
                    features.forEach( element => {   
                        let name = element.place_name;                
                        let my_address = element.address_name;
                        let city = element.address_name.split(' ')[1];
                        let my_link = element.place_url;
                        var position = new kakao.maps.LatLng(element.y, element.x);
                        let phone = element.phone;                        
                        if( city=='제주시') {
                            this.drawingMarker(cataindex, toRaw(this.map), position, name, my_address, my_link, phone);
                        }                        
                    });
                    // features.forEach( element => {   
                    //     // console.log(element)
                    //     let name = element.place_name;                
                    //     let my_address = element.address_name;
                    //     let my_link = element.place_url;
                    //     // let my_layer = element.id;
                    //     var position = new kakao.maps.LatLng(element.y, element.x);                        
                    //     // if( my_layer=='제주 식당') {
                    //         this.drawingMarker(cataindex, toRaw(this.map), position, name, my_address, my_link);
                    //     // }                        
                    // });
                } else {
                    jejurestMarkers.map( (cur) => cur.setMap(toRaw(this.map)));                    
                }
                jejurestIndex = false;
            } else {
                jejurestMarkers.map( (cur) => cur.setMap(null));
                jejurestIndex = true;
            }
        },

        displaySeogwipoCafe(){  
            cataindex=12;          
            if(seogwipocafeIndex){
                if ( seogwipocafeMarkers.length <=0 ) {                    
                    let features = jeju_cafe_all_data.features;
                    features.forEach( element => {   
                        let name = element.place_name;                
                        let my_address = element.address_name;
                        let city = element.address_name.split(' ')[1];
                        let my_link = element.place_url;
                        var position = new kakao.maps.LatLng(element.y, element.x);
                        let phone = element.phone;                        
                        if( city=='서귀포시') {
                            this.drawingMarker(cataindex, toRaw(this.map), position, name, my_address, my_link, phone);
                        }                        
                    });

                    // let features = jejucafe_data.features;
                    // features.forEach( element => {                        
                    //     let name = element.properties.Name;                
                    //     let my_point = element.geometry.coordinates;
                    //     let my_address = element.properties.address;
                    //     let my_link = element.properties.link;
                    //     let my_layer = element.properties.layer;
                    //     var position = new kakao.maps.LatLng(my_point[1], my_point[0]);                        
                    //     if( my_layer=='서귀포 카페') {                            
                    //         this.drawingMarker(cataindex, toRaw(this.map), position, name, my_address, my_link);
                    //     }                        
                    // });
                } else {
                    seogwipocafeMarkers.map( (cur) => cur.setMap(toRaw(this.map)));                    
                }
                seogwipocafeIndex = false;
            } else {
                seogwipocafeMarkers.map( (cur) => cur.setMap(null));
                seogwipocafeIndex = true;
            }
        },

        displaySeogwipoRest(){
            cataindex=13;
            if(seogwiporestIndex){
                if ( seogwiporestMarkers.length <=0 ) {
                    let features = jeju_rest_all_data.features;
                    features.forEach( element => {   
                        let name = element.place_name;                
                        let my_address = element.address_name;
                        let city = element.address_name.split(' ')[1];
                        let my_link = element.place_url;
                        var position = new kakao.maps.LatLng(element.y, element.x);
                        let phone = element.phone;                        
                        if( city=='서귀포시') {
                            this.drawingMarker(cataindex, toRaw(this.map), position, name, my_address, my_link, phone);
                        }                        
                    });                    
                    // let features = jejucafe_data.features;
                    // features.forEach( element => {                        
                    //     let name = element.properties.Name;                
                    //     let my_point = element.geometry.coordinates;
                    //     let my_address = element.properties.address;
                    //     let my_link = element.properties.link;
                    //     let my_layer = element.properties.layer;
                    //     var position = new kakao.maps.LatLng(my_point[1], my_point[0]);                        
                    //     if( my_layer=='서귀포 식당') {
                    //         this.drawingMarker(cataindex, toRaw(this.map), position, name, my_address, my_link);
                    //     }                        
                    // });
                } else {
                    seogwiporestMarkers.map( (cur) => cur.setMap(toRaw(this.map)));                    
                }
                seogwiporestIndex = false;
            } else {
                seogwiporestMarkers.map( (cur) => cur.setMap(null));
                seogwiporestIndex = true;
            }
        },

        drawingMarker(cataindex, map, position, name, my_address, my_link, phone) {

            // var position = new kakao.maps.LatLng(my_point[1], my_point[0]);
            // console.log( cataindex, my_address, my_link);
            var imageSrc = '';
            if( cataindex ==10) {
                imageSrc = '/img/jeju_cafe.png';
            } else if (cataindex ==11) {
                imageSrc = '/img/jeju_rest.png';
            } else if (cataindex ==12) {
                imageSrc = '/img/seogwipo_cafe.png';
            } else if (cataindex ==13) {
                imageSrc = '/img/seogwipo_rest.png';
            }

            var imageSize = new kakao.maps.Size(16, 17);
            var imageOption = {offset: new kakao.maps.Point(8, 9)};            
            var markerImage = new kakao.maps.MarkerImage(imageSrc, imageSize, imageOption)
            var marker = new kakao.maps.Marker({
                position : position,
                clickable: true,
                image : markerImage,
                map : map,
                title : name,
            });


            // var content =  '<div style="width:250px; height: 150px; padding: 5px;">';
            //     content += '    <a href='+my_link+' target="_blank"><strong> [ 오름명 : '+name+']</strong></a>';
            //     content += '    <div> 주소 : '+my_address+' </div>';
            //     content += '</div>';
            
            // var content = '<div style="width:250px; height: 100px; padding: 5px;"> <div></div>' +
            var content = '<div class="placeinfo">'+
                '   <a class="title" href="' + my_link + '" target="_blank" title="' + name + '">' + name + '</a>';
                content += '<div> <span title="' + my_address + '">' + my_address + '</span> </div>';   
                content += '      <span class="tel"> Tel. ' + phone + '</span>'; 
                content += '<div class="after"></div>';

            // console.log(content);

            var iwRemveable = true;
            var infowindow = new kakao.maps.InfoWindow({
                content: content,
                removable: iwRemveable,
            });            
            new kakao.maps.event.addListener(marker, 'click', function() {                                  
                infowindow.open(map, marker);
            })

            if( cataindex ==10) {
                jejucafeMarkers.push(marker);
            } else if (cataindex ==11) {
                jejurestMarkers.push(marker);
            } else if (cataindex ==12) {
                seogwipocafeMarkers.push(marker);
            } else if (cataindex ==13) {
                seogwiporestMarkers.push(marker);
            }
        },

        displaytest() {
            
            var container = document.getElementById("map");
            
            var options = {
                center: new kakao.maps.LatLng(33.40, 126.58),
                level: 8,
            };
            this.map = new kakao.maps.Map(container, options);
            var mmap = toRaw(this.map);
            
            new kakao.maps.event.addListener(mmap, 'idle', searchPlaces);            
            var ps = new kakao.maps.services.Places(mmap);

            function searchPlaces() {                
                ps.categorySearch('CE7', placesSearchCB, {useMapBounds:true}); 
            }
            
            function placesSearchCB (data, status, pagination) {
                if (status === kakao.maps.services.Status.OK) {
                    for (var i=0; i<data.length; i++) {
                        displayMarker(data[i]);
                        if(!(searchData.some(v => v.id === data[i].id))) {                            
                            searchData.push(data[i]);
                        }                       
                    }
                    console.log(searchData.length);       
                }
            }
            
            function displayMarker(place) {
                var marker = new kakao.maps.Marker({
                    map: mmap,
                    position: new kakao.maps.LatLng(place.y, place.x) 
                });

                kakao.maps.event.addListener(marker, 'click', function() {
                    infowindow.setContent('<div style="padding:5px;font-size:12px;">' + place.place_name + '</div>');
                    infowindow.open(mmap, marker);
                });
            }
        },
        
        myfileWrite() {


            let header ='{\n';
                header += '"type": "FeatureCollection",\n';
                header += '"name": "jeju_rest_all",\n';
                header += '"crs": { "type": "name", "properties": { "name": "urn:ogc:def:crs:OGC:1.3:CRS84" } },\n';
                header += '"features":\n';

            const data = header+JSON.stringify(searchData)+'\n}';
            const blob = new Blob([data], { type: 'text/plain' });
            const url = URL.createObjectURL(blob);
            const link = document.createElement('a');
            link.href = url;
            link.setAttribute('download', 'jeju_rest_alll.txt');
            document.body.appendChild(link);
            link.click();
            document.body.removeChild(link);



            
        },

        
    },
};
</script>

